<template>
  <div class="nutshell-form">
    <header>
      <h1>Me In A Nutshell</h1>
      <ul>
        <li v-for="person in persons" v-bind:key="'prev-'+person.idx">
          <img v-bind:src="previewImageFor(person)"
            v-bind:alt="'voorbeeld: '+person.firstName"
            v-bind:title="person.firstName"
            :class="{ blank: hasBlankPreview(person) }"
            @click="startEdit(person)">
        </li>
      </ul>
    </header>

    <section class="main" v-show="persons.length" v-cloak>
      <ul class="persons">
        <li
          v-for="person in persons"
          class="person"
          :key="person.idx"
          :class="{ editing: person === editPerson, not_editing: person !== editPerson, no_preview: hasBlankPreview(person) }"
          @click="startEdit(person)"
        >
          <span class="fullName">
            {{ fullName(person).trim() || (person.idx === 0 ? 'Hoe heet het kind?' : 'Wie is dit?') }}
          </span>
          
          <a class="remove" href="#" v-if="canBeRemoved(person)"
            @click="removePerson(person)">X</a>


          <ul class="form" v-if="person == editPerson">
            <li>
              <label for="firstName">Voornaam</label>
              <input
                id="firstName" type="text"
                :placeholder="person.idx === 0 ? 'Het begint allemaal met een naam...' : undefined"
                v-model="person.firstName"
                @blur="onBlur(person)"
                @input="onInput(person)"
              />
            </li>
            <li>
              <label for="lastName">Achternaam</label>
              <input id="lastName"
                v-model="person.lastName"
                @blur="onBlur(person)"
                @input="onInput(person)" />
            </li>
            <li>
              <label for="familyRole">Relatie to kind</label>
              <select id="familyRole"
                v-model="person.familyRole"
                :disabled="!canEditFamilyRole(person)"
                @blur="onBlur(person)"
                @input="onInput(person)">
                <option v-for="option in familyRoleOptions"
                  v-bind:disabled="option.value === 0"
                  v-bind:value="option.value"
                  v-bind:key="option.value">
                  {{ option.text }}
                </option>
              </select>
            </li>
            <li>
              <label for="age">Leeftijd</label>
              <input id="age"
                v-model.number="person.age"
                type="number"
                @blur="onBlur(person)"
                @input="onInput(person)" />
            </li>
            <li>
              <label for="deceased">Overleden</label>
              <input type="checkbox"
                id="deceased" v-model="person.deceased"
                @blur="onBlur(person)"
                @input="onInput(person)" />
            </li>
            <li>
              <label for="nationality">Nationaliteit</label>
              <select id="nationality"
                v-model="person.nationality"
                @blur="onBlur(person)"
                @input="onInput(person)">
                <option v-for="option in nationalityOptions"
                  v-bind:value="option.value"
                  v-bind:key="option.value">
                  {{ option.text }}
                </option>
              </select>
            </li>
            <li>
              <label for="birthPlace">Geboorteplaats</label>
              <input id="birthPlace" v-model="person.birthPlace"
                @blur="onBlur(person)"
                @input="onInput(person)" />
            </li>
            <li class="preview">
              <label></label>
              <img v-bind:src="previewImageFor(person)">
            </li>
          </ul>
        </li>
      </ul>

      <button v-if="canAdd()" class="add"
            @click="addPerson()">Voeg {{nth[persons.length]}} persoon toe</button>

      <button class="done"
            @click="onDone()">Ik heb alles ingevuld!</button>

    </section>


    <!-- rootUrl: <a :href="rootUrl">{{ rootUrl }}</a>

    <h1>QR Code Test</h1>
    <qrcode value="Hello, World!!" :options="{ width: 200 }"></qrcode> -->

  </div>
</template>

<script>

import Vue from 'vue';
import VueQrcode from '@chenfengyuan/vue-qrcode';

Vue.component(VueQrcode.name, VueQrcode);

class Form {
  setup() {
  }

  getQrCodePngData() {
    return document.getElementsByTagName('canvas')[0].toDataURL('image/png');
  }
}

// interface Person {
//   firstName: string;
//   lastName?: string;
//   familyRole?: number;
//   age?: number;
//   deceased: boolean = false;
//   nationality?: number;
//   birthplace?: string;
// }

export default {
  name: 'MeInANutshell',
  props: {
    rootUrl: String,
    maxPersons: Number
  },
  data: () => {
    const child = {idx: 0, familyRole: 0};
    return {
      fakeLoadingDuration: 2500, // ms
      onInputDelay: 1000, // ms

      persons: [
        child
      ],
      nth: [
        'eerste',
        'tweede',
        'derde',
        'vierde',
        'vijfde',
        'zesde',
        'zevende',
        'achtste',
        'negende',
        'tiende',
        'elfde',
        'twaalfde',
        'dertiende',
        'veertiende',
        'vijftiende',
        'zestiende',
        'zeventiende',
        'achttiende',
        'negentiende',
        'twintigste',
        'eenentwintigste',
        'tweeëntwintigste',
        'drieëntwintigste',
        'vierentwintigstte'
      ],
      familyRoleOptions: [
        { value: undefined, text: '-- selecteer de relatie tot het kind --'},
        { value: 0, text: 'kind'},
        { value: 1, text: '(stief-)ouder'},
        { value: 2, text: '(stief-)zus/(stief-)broer, halfzus/halfbroer'},
        { value: 3, text: 'oma/opa'},
        { value: 4, text: 'tante/oom'},
        { value: 5, text: 'overgroot moeder/vader'},
        { value: 6, text: 'voogd'},
        { value: 7, text: 'overig'}
      ],
      nationalityOptions: [
        { value: undefined, text: '-- selecteer een nationaliteit --'},
        { value: 0, text: 'Nederlands'},
        { value: 1, text: 'Belgisch'},
        { value: 2, text: 'Duits'},
      ],
      editPerson: child
    }
  },
  directives: {
    "person-focus": function(el, binding) {
      if (binding.value) {
        el.focus();
      }
    }
  },
  methods: {
    canAdd: function() {
      return this.persons.length < this.maxPersons;
    },
    canBeRemoved: function(person) {
      return person.idx > 0;
    },
    canEditFamilyRole: function(person) {
      return person.idx > 0;
    },

    addPerson: function() {
      this.persons = [...this.persons, {id: this.persons.length}]
        .map((p, idx) => ({...p, idx: idx}))
      this.editPerson = this.persons[this.persons.length-1];
    },
    removePerson: function(person, confirmed) {
      if (person.idx < 1) {
        console.warn("can't remove child");
        return;
      }

      if (!confirmed && !confirm(`Weet je zeker dat je de persoon ${person.firstName} wilt verwijderen?`)) {
        return;
      }

      const ps = [...this.persons]
      ps.splice(ps.indexOf(person), 1);
      this.persons = ps;

      if (this.persons.length === 1) {
        this.editPerson = this.persons[0];
      }
    },
    startEdit: function(person) {
      this.editPerson = person;
    },
    doneEdit: function(person) {
      if (this.editPerson === person) {
        this.cancelEdit(person);
      }
    },
    cancelEdit: function(person) {
      if (this.editPerson === person) {
        this.editPerson = null;
      }
    },
    onBlur: function(person) {
      this.updatePreviewFor(person);
    },
    onInput: function(person) {
      // stop already scheduled delayed execution
      if (this.onInputTimer) {
        clearTimeout(this.onInputTimer);
      }

      // schedule new delayed execution
      this.onInputTimer = setTimeout(() =>
        this.updatePreviewFor(person),
        this.onInputDelay);
    },
    updatePreviewFor: function(person) {
      // if data complete
      if (this.isComplete(person)) {
        this.loadImage(person);
      } else if (this.hasLoadedImage(person)) {
        this.unloadImage(person);
      }
    },
    isComplete: function(person) {
      return person.firstName &&
        person.lastName &&
        person.familyRole !== undefined &&
        person.age !== undefined &&
        person.nationality !== undefined &&
        person.birthPlace;
        
    },
    fullName: function(person) {
      return [person.firstName, person.lastName].join(' ')
    },
    unloadImage: function(person) {
      person.isLoadingImage = false;
      person.previewUrl = null;
      this.persons = [...this.persons]
    },
    loadImage: function(person) {
      person.isLoadingImage = true;
      this.persons = [...this.persons]
      setTimeout(() => {
        person.isLoadingImage = false;
        person.previewUrl = 'preview-render.png';
        this.persons = [...this.persons]
      }, this.fakeLoadingDuration);
    },
    isLoadingImage: function(person) {
      return person.isLoadingImage === true;
    },
    hasLoadedImage: function(person) {
      return person.previewUrl && !this.isLoadingImage(person);
    },
    hasBlankPreview: function(person) {
      return !this.isLoadingImage(person) && !this.hasLoadedImage(person)
    },
    previewImageFor: function(person) {
      return this.isLoadingImage(person)
        ? 'spinner.gif'
        : this.hasLoadedImage(person)
          ? person.previewUrl
          : 'blank.png';
    },
    onDone: function() {
      const incompletePersons = this.persons.filter(p => !this.isComplete(p));

      if (incompletePersons.length > 0) {
        alert('Deze personen zijn nog niet volledig ingevuld:\n' +
          incompletePersons
            .map(p => `#${p.idx + 1}: ${this.fullName(p).trim() || '[heeft nog geen naam]'}`)
            .join('\n'));
        return;
      }

      if (this.persons.length < 2) {
        alert('Het wordt een mooier resultaat als je meer personen toevoegt!');
        return;
      }

      alert('Het opslaan werkt nu nog niet, op dit moment zou deze informatie worden opgeslagen en de gebruiker de Me In A Nutshell producten in de webshop te zien krijgen.');
    }
  },
  created: () => {
  },
  mounted: () => {
    const form = new Form();
    form.setup();
  }
}

</script>

